@use "sass:math";

//  ===============================================================================================================================================================================================================================================================================================
@import "variables";

//   ===========================================================================================================================================================================================================================================================================
@import "mixins";

// fonts
@import "fonts";

//resets
@import "reset";



//  
html {
	height: 100%;

	body {
		min-height: 100%;
		display: flex;
		flex-direction: column;
		overflow-x: hidden;
		@include font-default();
		//  footer
		> main {
			flex: 1 1 auto;
		}
	}

	//   ======================================================================================================================================================================================================================
	.container {
		max-width: $maxWidthContainer + px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		padding-left: 15px;
		padding-right: 15px;
	}




}

